.sfCon{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    margin: 30px 0px;
    padding: 20px;
    box-shadow: 0px 0px 15px -3px rgba(0,0,0,0.75);   
}
.fCon, .sCon, .bCon{
    display: flex;
    align-items: center;
}
.fCon>label, .sCon>label{
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
}
.fCon>select, .sCon>select{
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    background: none;
    text-transform: capitalize;
}
option{
    text-transform: capitalize;
}
